<template>
  <div class="interface-detail-view">
    <div class="view-header" v-if="currentInterface">
      <h3>{{ currentInterface.name }}</h3>
      <div class="interface-basic-info">
        <span class="interface-method" :class="'method-' + currentInterface.method.toLowerCase()">
          {{ currentInterface.method }}
        </span>
        <span class="interface-path">{{ currentInterface.path }}</span>
      </div>
    </div>
    <div class="no-data" v-else>
      <el-empty description="请选择一个接口查看详情"></el-empty>
    </div>
    
    <!-- 接口详情内容 -->
    <div class="detail-content" v-if="currentInterface">
      <el-tabs v-model="activeTab" type="border-card">
        <!-- 接口信息标签页 -->
        <el-tab-pane label="接口信息" name="info">
          <el-descriptions :column="1" border>
            <el-descriptions-item label="接口名称">{{ currentInterface.name }}</el-descriptions-item>
            <el-descriptions-item label="请求方式">{{ currentInterface.method }}</el-descriptions-item>
            <el-descriptions-item label="接口路径">{{ currentInterface.path }}</el-descriptions-item>
            <el-descriptions-item label="接口描述" v-if="currentInterface.description">
              {{ currentInterface.description }}
            </el-descriptions-item>
            <el-descriptions-item label="所属文件">{{ currentFile?.filename || '未知文件' }}</el-descriptions-item>
          </el-descriptions>
        </el-tab-pane>
        
        <!-- 请求参数标签页 -->
        <el-tab-pane label="请求参数" name="params">
          <el-table :data="interfaceParams" stripe style="width: 100%" v-if="interfaceParams.length > 0">
            <el-table-column prop="name" label="参数名" width="150"></el-table-column>
            <el-table-column prop="param_type" label="参数类型" width="150"></el-table-column>
            <el-table-column prop="required" label="是否必填" width="100">
              <template slot-scope="scope">
                <el-tag type="success" v-if="scope.row.required">必填</el-tag>
                <el-tag type="info" v-else>可选</el-tag>
              </template>
            </el-table-column>
            <el-table-column prop="description" label="描述"></el-table-column>
            <el-table-column prop="example" label="示例值"></el-table-column>
          </el-table>
          <el-empty description="暂无请求参数" v-else></el-empty>
        </el-tab-pane>
        
        <!-- 响应参数标签页 -->
        <el-tab-pane label="响应参数" name="responses">
          <el-table :data="interfaceResponses" stripe style="width: 100%" v-if="interfaceResponses.length > 0">
            <el-table-column prop="name" label="字段名" width="150"></el-table-column>
            <el-table-column prop="response_type" label="字段类型" width="150"></el-table-column>
            <el-table-column prop="description" label="描述"></el-table-column>
            <el-table-column prop="example" label="示例值"></el-table-column>
          </el-table>
          <el-empty description="暂无响应参数" v-else></el-empty>
        </el-tab-pane>
        
        <!-- Mock配置标签页 -->
        <el-tab-pane label="Mock配置" name="mock">
          <el-form :model="mockForm" label-width="100px">
            <el-form-item label="启用Mock">
              <el-switch v-model="mockForm.enabled"></el-switch>
            </el-form-item>
            <el-form-item label="默认生成条数">
              <el-input-number v-model="mockForm.default_count" :min="1" :max="1000" :step="1"></el-input-number>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="updateMockConfig">保存配置</el-button>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        
        <!-- 发送请求标签页 -->
        <el-tab-pane label="发送请求" name="request">
          <el-form :model="requestForm" label-width="80px">
            <el-form-item label="请求参数">
              <el-input
                type="textarea"
                v-model="requestForm.params"
                :rows="6"
                placeholder="请输入JSON格式的请求参数"
              ></el-input>
            </el-form-item>
            <el-form-item label="Mock条数">
              <el-input-number v-model="requestForm.mock_count" :min="1" :max="1000" :step="1"></el-input-number>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="sendRequest" :loading="sending">发送请求</el-button>
              <el-button @click="resetRequestForm">重置</el-button>
            </el-form-item>
          </el-form>
          
          <!-- 响应结果 -->
          <div class="response-result" v-if="responseResult">
            <h4>响应结果</h4>
            <el-divider></el-divider>
            <div class="response-status" :class="responseResult.code === 0 ? 'status-success' : 'status-error'">
              <span class="status-code">{{ responseResult.code }}</span>
              <span class="status-message">{{ responseResult.message }}</span>
            </div>
            <div class="response-data">
              <el-input
                type="textarea"
                v-model="responseResultStr"
                :rows="10"
                readonly
                placeholder="响应数据"
              ></el-input>
            </div>
          </div>
        </el-tab-pane>
        
        <!-- 请求日志标签页 -->
        <el-tab-pane label="请求日志" name="logs">
          <div class="log-list">
            <el-empty description="暂无请求日志" v-if="requestLogs.length === 0"></el-empty>
            <el-timeline v-else>
              <el-timeline-item v-for="log in requestLogs" :key="log.id" :timestamp="formatDateTime(log.request_time)">
                <div class="log-item">
                  <div class="log-header">
                    <span class="log-method" :class="'method-' + log.method.toLowerCase()">{{ log.method }}</span>
                    <span class="log-path">{{ log.path }}</span>
                    <span class="log-status" :class="log.response_status === 200 ? 'status-success' : 'status-error'">
                      {{ log.response_status }}
                    </span>
                    <span class="log-time">{{ (log.execution_time || 0).toFixed(2) }}ms</span>
                  </div>
                  <div class="log-content" v-if="log.params">
                    <div class="log-title">请求参数:</div>
                    <pre>{{ log.params }}</pre>
                  </div>
                  <div class="log-content" v-if="log.response_body">
                    <div class="log-title">响应结果:</div>
                    <pre>{{ log.response_body }}</pre>
                  </div>
                </div>
              </el-timeline-item>
            </el-timeline>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InterfaceDetailView',
  data() {
    return {
      activeTab: 'info',
      sending: false,
      responseResult: null,
      responseResultStr: '',
      currentInterface: null,
      currentFile: null,
      interfaceParams: [],
      interfaceResponses: [],
      requestLogs: [],
      mockForm: {
        enabled: true,
        default_count: 10
      },
      requestForm: {
        params: '{}',
        mock_count: 10
      }
    }
  },
  created() {
    // 监听Vuex中当前接口的变化
    this.$store.watch(
      state => state.currentInterface,
      (newInterface) => {
        this.currentInterface = newInterface
        if (newInterface) {
          this.loadInterfaceDetail(newInterface.id)
        }
      },
      { deep: true }
    )
    
    // 监听Vuex中当前文件的变化
    this.$store.watch(
      state => state.currentFile,
      (newFile) => {
        this.currentFile = newFile
      },
      { deep: true }
    )
    
    // 监听Vuex中请求日志的变化
    this.$store.watch(
      state => state.requestLogs,
      (newLogs) => {
        this.requestLogs = newLogs
      },
      { deep: true }
    )
  },
  methods: {
    // 加载接口详情
    async loadInterfaceDetail(interfaceId) {
      try {
        // 加载接口参数
        const paramsResponse = await this.$axios.get(`/api/interfaces/${interfaceId}/params`)
        this.interfaceParams = paramsResponse.data
        
        // 加载接口响应字段
        const responsesResponse = await this.$axios.get(`/api/interfaces/${interfaceId}/responses`)
        this.interfaceResponses = responsesResponse.data
        
        // 加载Mock配置
        const mockResponse = await this.$axios.get(`/api/interfaces/${interfaceId}/mock-config`)
        this.mockForm = mockResponse.data
      } catch (error) {
        console.error('加载接口详情失败:', error)
      }
    },
    
    // 更新Mock配置
    async updateMockConfig() {
      try {
        await this.$axios.put(`/api/interfaces/${this.currentInterface.id}/mock-config`, this.mockForm)
        this.$message.success('Mock配置更新成功')
      } catch (error) {
        this.$message.error('Mock配置更新失败')
      }
    },
    
    // 发送请求
    async sendRequest() {
      try {
        this.sending = true
        this.responseResult = null
        this.responseResultStr = ''
        
        // 构造请求URL
        const url = `/api/dynamic${this.currentInterface.path}`
        
        // 解析请求参数
        let params = {}
        try {
          params = JSON.parse(this.requestForm.params)
        } catch (e) {
          this.$message.error('请求参数格式错误，请输入JSON格式')
          this.sending = false
          return
        }
        
        // 构造完整请求参数
        const requestData = {
          params: params,
          mock_count: this.requestForm.mock_count
        }
        
        // 发送请求
        const response = await this.$axios({
          method: this.currentInterface.method.toLowerCase(),
          url: url,
          data: requestData,
          headers: {
            'Content-Type': 'application/json'
          }
        })
        
        // 保存响应结果
        this.responseResult = response.data
        this.responseResultStr = JSON.stringify(response.data, null, 2)
        
        // 记录请求日志
        this.$message.success('请求发送成功')
      } catch (error) {
        this.$message.error('请求发送失败')
        console.error('发送请求失败:', error)
      } finally {
        this.sending = false
      }
    },
    
    // 重置请求表单
    resetRequestForm() {
      this.requestForm = {
        params: '{}',
        mock_count: 10
      }
      this.responseResult = null
      this.responseResultStr = ''
    },
    
    // 格式化日期时间
    formatDateTime(dateTime) {
      const date = new Date(dateTime)
      return date.toLocaleString()
    }
  }
}
</script>

