<template>
  <div class="interface-list-view">
    <div class="view-header">
      <h3>接口列表</h3>
      <div class="view-actions">
        <el-select v-model="filterMethod" placeholder="请求方式" clearable size="small" @change="handleFilterChange">
          <el-option label="GET" value="GET"></el-option>
          <el-option label="POST" value="POST"></el-option>
          <el-option label="PUT" value="PUT"></el-option>
          <el-option label="DELETE" value="DELETE"></el-option>
          <el-option label="PATCH" value="PATCH"></el-option>
        </el-select>
        <el-input
          v-model="searchKeyword"
          placeholder="搜索接口名称或路径"
          size="small"
          prefix-icon="el-icon-search"
          clearable
          @input="handleSearch"
        ></el-input>
      </div>
    </div>
    
    <!-- 接口列表 -->
    <div class="interface-list">
      <el-empty description="暂无接口数据" v-if="filteredInterfaces.length === 0"></el-empty>
      <el-card v-for="intf in filteredInterfaces" :key="intf.id" class="interface-card" @dblclick="selectInterface(intf)">
        <div class="interface-header">
          <div class="interface-method" :class="'method-' + intf.method.toLowerCase()">
            {{ intf.method }}
          </div>
          <div class="interface-name">{{ intf.name }}</div>
        </div>
        <div class="interface-path">{{ intf.path }}</div>
        <div class="interface-description" v-if="intf.description">
          {{ intf.description }}
        </div>
        <div class="interface-actions">
          <el-button size="mini" @click="selectInterface(intf)">
            <i class="el-icon-view"></i> 查看
          </el-button>
          <el-button size="mini" type="primary" @click="generateInterface(intf)">
            <i class="el-icon-rocket"></i> 生成服务
          </el-button>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'InterfaceListView',
  data() {
    return {
      filterMethod: '',
      searchKeyword: '',
      interfaces: []
    }
  },
  computed: {
    // 过滤后的接口列表
    filteredInterfaces() {
      return this.interfaces.filter(intf => {
        // 按请求方式过滤
        if (this.filterMethod && intf.method !== this.filterMethod) {
          return false
        }
        // 按关键词搜索
        if (this.searchKeyword) {
          const keyword = this.searchKeyword.toLowerCase()
          return intf.name.toLowerCase().includes(keyword) || 
                 intf.path.toLowerCase().includes(keyword) ||
                 (intf.description && intf.description.toLowerCase().includes(keyword))
        }
        return true
      })
    }
  },
  created() {
    // 初始化加载接口列表
    this.loadInterfaces()
    
    // 监听Vuex中接口列表的变化
    this.$store.watch(
      state => state.interfaces,
      (newInterfaces) => {
        this.interfaces = newInterfaces
      },
      { deep: true }
    )
    
    // 监听Vuex中当前文件的变化，重新加载接口列表
    this.$store.watch(
      state => state.currentFile,
      (newFile) => {
        if (newFile) {
          this.$store.dispatch('loadInterfaces', newFile.id)
        }
      }
    )
  },
  methods: {
    // 加载接口列表
    async loadInterfaces() {
      try {
        const currentFile = this.$store.state.currentFile
        await this.$store.dispatch('loadInterfaces', currentFile ? currentFile.id : null)
        this.interfaces = this.$store.state.interfaces
      } catch (error) {
        this.$message.error('加载接口列表失败')
      }
    },
    
    // 处理筛选条件变化
    handleFilterChange() {
      // 筛选条件变化时自动过滤
    },
    
    // 处理搜索
    handleSearch() {
      // 搜索关键词变化时自动过滤
    },
    
    // 选择接口
    selectInterface(intf) {
      this.$store.commit('SET_CURRENT_INTERFACE', intf)
      // 加载接口详情
      this.$store.dispatch('loadInterfaceDetail', intf.id)
      // 加载Mock配置
      this.$store.dispatch('loadMockConfig', intf.id)
      // 加载请求日志
      this.$store.dispatch('loadRequestLogs', intf.id)
      // 更新路由
      this.$router.push(`/interface/${intf.id}`)
    },
    
    // 生成接口服务
    async generateInterface(intf) {
      try {
        const response = await this.$axios.post(`/interfaces/generate/${intf.id}`)
        this.$message.success(response.data.message)
      } catch (error) {
        this.$message.error('生成接口服务失败')
      }
    }
  }
}
</script>

