<template>
  <div class="file-upload-view">
    <div class="view-header">
      <h3>文件管理</h3>
      <el-button type="primary" size="small" @click="showUploadDialog = true">
        <i class="el-icon-upload"></i> 上传文件
      </el-button>
    </div>
    
    <!-- 上传文件对话框 -->
    <el-dialog
      title="上传接口文档"
      :visible.sync="showUploadDialog"
      width="500px"
      @close="resetUploadForm"
    >
      <el-upload
        class="upload-demo"
        drag
        action="/api/files/upload"
        :on-success="handleUploadSuccess"
        :on-error="handleUploadError"
        :before-upload="beforeUpload"
        multiple
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">
          支持上传任意格式的文件，单文件大小不超过10MB
        </div>
      </el-upload>
    </el-dialog>
    
    <!-- 文件列表 -->
    <div class="file-list">
      <el-empty description="暂无上传的文件" v-if="files.length === 0"></el-empty>
      <el-card v-for="file in files" :key="file.id" class="file-card" @dblclick="selectFile(file)">
        <div class="file-info">
          <div class="file-name">{{ file.filename }}</div>
          <div class="file-meta">
            <span class="file-type">{{ file.file_type }}</span>
            <span class="file-size">{{ formatFileSize(file.size) }}</span>
            <span class="file-time">{{ formatDateTime(file.uploaded_at) }}</span>
          </div>
        </div>
        <div class="file-actions">
          <el-button size="mini" @click="selectFile(file)">
            <i class="el-icon-folder-opened"></i> 查看
          </el-button>
          <el-button size="mini" type="success" @click="generateInterfaces(file)"
            :disabled="file.parsed">
            <i class="el-icon-document"></i> 生成接口
          </el-button>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: 'FileUploadView',
  data() {
    return {
      showUploadDialog: false,
      files: []
    }
  },
  created() {
    // 初始化加载文件列表
    this.loadFiles()
  },
  methods: {
    // 加载文件列表
    async loadFiles() {
      try {
        const response = await this.$axios.get('/api/files')
        this.files = response.data
        this.$store.commit('SET_FILES', response.data)
      } catch (error) {
        this.$message.error('加载文件列表失败')
      }
    },
    
    // 重置上传表单
    resetUploadForm() {
      // 重置上传表单逻辑
    },
    
    // 文件上传前的校验
    beforeUpload(file) {
      const fileSize = file.size / 1024 / 1024 // 转换为MB
      if (fileSize > 10) {
        this.$message.error('文件大小不能超过10MB')
        return false
      }
      return true
    },
    
    // 文件上传成功处理
    handleUploadSuccess() {
      this.$message.success('文件上传成功')
      this.loadFiles() // 重新加载文件列表
    },
    
    // 文件上传失败处理
    handleUploadError() {
      this.$message.error('文件上传失败')
    },
    
    // 格式化文件大小
    formatFileSize(size) {
      if (size == null || isNaN(size) || size < 0) {
        return '0 B'
      }
      if (size < 1024) {
        return size + ' B'
      } else if (size < 1024 * 1024) {
        return (size / 1024).toFixed(2) + ' KB'
      } else {
        return (size / (1024 * 1024)).toFixed(2) + ' MB'
      }
    },
    
    // 格式化日期时间
    formatDateTime(dateTime) {
      if (!dateTime) {
        return '未上传'
      }
      const date = new Date(dateTime)
      if (isNaN(date.getTime())) {
        return '无效日期'
      }
      return date.toLocaleString()
    },
    
    // 选择文件
    selectFile(file) {
      this.$store.commit('SET_CURRENT_FILE', file)
      // 加载该文件的接口列表
      this.$store.dispatch('loadInterfaces', file.id)
    },
    
    // 生成接口
    generateInterfaces() {
      // TODO: 实现生成接口逻辑
      this.$message.info('生成接口功能开发中')
    }
  }
}
</script>

